<template>
    <ul class="zd-common-clearFix zd-common-lh30">
      <li class="zd-common-fl zd-common-wp50 zd-common-mb10"><span class="zd-common-w100 zd-common-dib zd-common-tr">物品名称：</span><span class="zd-common-font-weight">{{ allDetail.name }}</span></li>
      <li class="zd-common-fl zd-common-wp50 zd-common-mb10"><span class="zd-common-w100 zd-common-dib zd-common-tr">物品编码：</span><span class="zd-common-font-weight">{{ allDetail.code }}</span></li>
      <li class="zd-common-fl zd-common-wp50 zd-common-mb10"><span class="zd-common-w100 zd-common-dib zd-common-tr">物品分类：</span><span class="zd-common-font-weight">{{ allDetail.goodsKindName }}</span></li>
      <li class="zd-common-fl zd-common-wp50 zd-common-mb10"><span class="zd-common-w100 zd-common-dib zd-common-tr">品牌：</span><span class="zd-common-font-weight">{{ allDetail.brand }}</span></li>
      <li class="zd-common-fl zd-common-wp50 zd-common-mb10"><span class="zd-common-w100 zd-common-dib zd-common-tr">规格 / 型号：</span><span class="zd-common-font-weight">{{ allDetail.size }}</span></li>
      <li class="zd-common-fl zd-common-wp50 zd-common-mb10"><span class="zd-common-w100 zd-common-dib zd-common-tr">单位：</span><span class="zd-common-font-weight">{{ allDetail.unit }}</span></li>
      <li class="zd-common-fl zd-common-wp50 zd-common-mb10"><span class="zd-common-w100 zd-common-dib zd-common-tr">最高库存：</span><span class="zd-common-font-weight">{{ allDetail.maxInventory }}</span></li>
      <li class="zd-common-fl zd-common-wp50 zd-common-mb10"><span class="zd-common-w100 zd-common-dib zd-common-tr">最低库存：</span><span class="zd-common-font-weight">{{ allDetail.minInventory }}</span></li>
    </ul>
</template>

<script>
    export default {
      props:{
        allDetail:{
          type:Object
        }
      }
    }
</script>

<style scoped>

</style>



