<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10" @click="exportHandle">
        <Icon type="ios-open-outline" class="zd-common-fs18 zd-common-mr5 zd-common-fl"/>导出
      </Button>
      <div class="zd-common-fr">
        <DatePicker v-model="date" :editable="false" type="daterange" :options="dateOptions" placeholder="日期"
                    class="zd-common-input-h38 zd-common-fl zd-common-mr10 zd-cm-w220" @on-change="changeDate"></DatePicker>
        <Select v-model="warehouse" :clearable="true" placeholder="全部仓库" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10">
          <Option v-for="item in warehouseArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <div class="zd-cm-tree zd-common-w300 zd-common-fl zd-common-mr10">
          <treeselect v-model="kind" :multiple="true" :options="treeOptions" :normalizer="normalizer" :limit="2" :limitText="limitHandle" noOptionsText="暂无数据" :clearable="false" placeholder="选择物品分类" @close="onSelect"/>
        </div>
        <Select clearable v-model="type" placeholder="选择入库类型" class="zd-common-w150 zd-common-mr10 zd-common-input-h38 zd-common-fl">
          <Option v-for="item in typeArr" :value="item.code" :key="item.code">{{ item.name }}</Option>
        </Select>
        <Input v-model="name" :maxlength="50" placeholder="入库单号/物品名称" class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10"/>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>
    </div>
    <!--入库详情-->
    <Modal v-model="inventoryModal" :mask-closable="false" :width="600">
      <h3 slot="header" class="zd-common-modal-header">
        <span>入库详情 - {{ allDetail.name }}</span>
      </h3>
      <good-detail :allDetail="allDetail"></good-detail>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
  import GoodDetail from '../../components/goodDetail'
  import Treeselect from '@riophae/vue-treeselect'
  import '@riophae/vue-treeselect/dist/vue-treeselect.css'
  export default {
    components: {
      Treeselect,
      GoodDetail
    },
    data() {
      return {
        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '入库单号',
            align: 'center',
            key: 'orderNO'
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
            render: (h, params) => {
              return h('div', [
                h('span', {
                  class:'zd-common-primary-c zd-common-cp',
                  on:{
                    click: () => {
                      this.showDetail(params.row.id);
                    }
                  }
                },params.row.name),
              ]);
            }
          },
          {
            title: '数量',
            align: 'center',
            key: 'number'
          },
          {
            title: '单价',
            align: 'center',
            key: 'price',
          },
          {
            title: '金额',
            align: 'center',
            key: 'amount',
          },
          {
            title: '入库仓库',
            align: 'center',
            key: 'warehouse',

          },
          {
            title: '入库类型',
            align: 'center',
            key: 'type',

          },
          {
            title: '入库日期',
            align: 'center',
            key: 'date',

          },
          {
            title: '购买人',
            align: 'center',
            key: 'purchaser',

          },
          {
            title: '制单人',
            align: 'center',
            key: 'orderPerson',

          },
          {
            title: '制单日期',
            align: 'center',
            key: 'orderDate',

          },
        ],
        data: [],
        selectLoading:false,
        pageSize:10,
        pageNum:1,
        total:0,
        //查询
        date:[],
        dateOptions: this.dateRange,
        warehouseArr:[],
        warehouse:'',
        kind:null,
        kindIdArr:[],
        type:'',
        typeArr:[],
        name:'',
        treeOptions:[],
        startDate:'',
        endDate:'',
        normalizer(node) {
          return {
            id: node.id,
            label: node.name,
            children: node.children,
          }
        },
        //入库详情
        allDetail:{},
        inventoryModal:false,
        saveLoading:false
      }
    },
    created(){
      this.getIntoKindList().then((response)=>{
        this.typeArr = response.data
      });
      this.getChargedStoreRoom().then((response)=>{
        this.warehouseArr = response.data;
      });
      this.getKind();
      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
      this.date = [start,end];
      const endDate = end.toLocaleDateString().split('/');
      const startDate = start.toLocaleDateString().split('/');
      this.endDate = endDate[0]+"-"+this.fillZero(endDate[1])+"-"+this.fillZero(endDate[2]);
      this.startDate = startDate[0]+"-"+this.fillZero(startDate[1])+"-"+this.fillZero(startDate[2]);
      this.intoWarehouseStatistics(this.pageNum,this.pageSize,this.startDate,this.endDate,this.warehouse,this.kindIdArr.join(','),this.name,this.type);
    },
    methods: {
      //日期
      changeDate(arr){
        this.startDate = arr[0];
        this.endDate = arr[1];
      },
      //物品种类
      onSelect(value,instanceId){
        this.kindIdArr = value;
      },
      //获取分类
      getKind(){
        this.treeOptions= [];
        this.getGoodsKindTree().then((res)=>{
          this.treeOptions = res.data;
          // this.recursionFnOne(this.treeOptions,res.data);
        })
      },
      //入库详情
      showDetail(id){
        this.inventoryModal = true;
        this.goodsStatisticsDetail(id);
      },
      goodsStatisticsDetail(id) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getGoodsInfoDetail.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            this.allDetail = response.data;
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },

      //列表
      intoWarehouseStatistics(pageNum,pageSize,startDate,endDate,warehouseIds,goodsKindIds,goodsName,kind) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!intoWarehouseStatistics.action',
          data:{
            pageNum:pageNum,
            pageSize:pageSize,
            startDate:startDate,
            endDate:endDate,
            warehouseIds:warehouseIds,
            goodsKindIds:goodsKindIds,
            goodsName:goodsName,
            kind:kind
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.list.length>0){
              aData.list.map((item)=>{
                this.data.push({
                  id:item.goodsInfoId,
                  orderNO:item.num,
                  name:item.goodsInfoName,
                  number:item.count,
                  price:item.unitPrice,
                  amount:item.unitPrice*item.count,
                  warehouse:item.warehouseName,
                  type:item.intoKind,
                  date:item.date,
                  purchaser:item.buyName,
                  orderPerson:item.userName,
                  orderDate:item.productDate
                })

              })
            }
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },

      //分页
      changePage(num) {
        this.pageNum = num;
        this.intoWarehouseStatistics(this.pageNum,this.pageSize,this.startDate,this.endDate,this.warehouse,this.kindIdArr.join(','),this.name,this.type);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.intoWarehouseStatistics(this.pageNum,this.pageSize,this.startDate,this.endDate,this.warehouse,this.kindIdArr.join(','),this.name,this.type);
      },
      //查询
      selectHandle(){
        this.selectLoading = true;
        this.intoWarehouseStatistics(this.pageNum,this.pageSize,this.startDate,this.endDate,this.warehouse,this.kindIdArr.join(','),this.name,this.type);
      },
      //导出
      exportHandle(){
        location.href = this.serverPath + "/sr/storeroomData!exportIntoWarehouseStatistics.action?warehouseIds="+this.warehouse+"&goodsKindIds="+this.kindIdArr.join(',')+"&goodsName="+this.name+"&startDate="+this.startDate+"&endDate="+this.endDate+"&type="+this.type;
      }
    },
    mounted(){

    }
  }
</script>
<style lang="less" scoped>

</style>



